import React from 'react';
import './App.css';
import Immutable, { is } from "immutable";
import all from "./brickLevels";
var height = window.innerHeight,
    width= window.innerWidth;
    var startHeight = 30,
    startWidth = 120,
    bottomGap= 40,
    startTop= height - bottomGap -startHeight,
    lineMax = height / 1.5,
    lineLength= 0,
    gridCount=6,
    brickSpacing = 5,
    gridEdge= 2 * (width / 3);

export default class Bricks extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            lines: new Immutable.List(),
            relArray : new Immutable.List(),
            isDrawing: false,
            lineColor: "pink",
            isAlive: false,
            start: [[width/2 - startWidth/2, height-startHeight - bottomGap],[width/2 + startWidth/2, height- startHeight- bottomGap], [width/2 + startWidth/2, height-bottomGap], [width/2 - startWidth/2,height-bottomGap]],
            arr: [],
            relPointer: 0,
            points: 0,
            highScore: 0,
            alerted: false
        }
    }
    componentDidMount() {
        document.querySelector(".drawArea").style.height = height + "px";
        document.querySelector(".drawArea").style.width = width + "px";
        this.createLevel();
        this.totalBlanks = 0;
        this.refs.drawArea.addEventListener('touchmove', (event) => this.handleMouseMove(event), false)
        this.refs.drawArea.addEventListener('touchend', (event) => this.handleMouseUp(event), false)
        this.refs.drawArea.addEventListener('touchstart', (event) => this.handleMouseDown(event), false)
    }
    insertNew() {
        var arr= this.state.arr;
        var animationStep = 10;
        var animArr = [];
        for(var j=0; j < gridCount; j++) {
            var blackSpaces = 0;
            var animStep = 1;
        for(var i = gridCount-1; i>=0; i--){
                if(arr[i][j].isPopped){
                    blackSpaces++;
                }
                else {
                    if(blackSpaces > 0) {
                        arr[i + blackSpaces][j] = arr[i][j];
                        animArr.push({
                            row: i + blackSpaces,
                            column: j,
                            toValue: ((gridEdge / gridCount) - brickSpacing) * blackSpaces
                        })
                        // var topFactor = (arr[i + blackSpaces][j].style.top);
                        // (arr[i + blackSpaces][j].style = {
                        //     ...arr[i + blackSpaces][j].style,
                        //     top:  topFactor + (blackSpaces * (gridEdge / gridCount)) > gridEdge ? topFactor : topFactor + (blackSpaces * (gridEdge / gridCount))
                        // })

                    }
                }
            }
            while(blackSpaces > 0) {
                blackSpaces--;
                var type = 'pop';
                this.totalBlanks++;
                if((this.totalBlanks % 5) === 0) Math.random() > 0.5 ? type = 'bomb' : type= 'pop';
                else if((this.totalBlanks % 4) === 0) Math.random() > 0.5 ? type = 'vertical'  : type= 'pop';
                else if((this.totalBlanks % 3) === 0) Math.random() > 0.5 ? type = 'horizontal'  : type= 'pop';
                arr[blackSpaces][j] = {
                    type: type,
                    img: this.totalBlanks % 3 === 0 ? 'pop2' : this.totalBlanks % 4 === 0 ? 'pop3' : 'pop',
                    className: type === 'pop' ? 'brick' : '',
                    isPopped: false,
                    style: {
                        top: brickSpacing,
                        left: brickSpacing + ((width-gridEdge) / 2) + j * (gridEdge / gridCount),
                        height: gridEdge / gridCount - brickSpacing,
                        width: gridEdge / gridCount - brickSpacing,
                        borderRadius: brickSpacing,
                        position: 'absolute'
                    }
                }
                animArr.push({
                    row: blackSpaces,
                    column: j,
                    toValue: (blackSpaces * ((gridEdge / gridCount) - brickSpacing))
                })
            }
        }
        this.animateBoxes(animArr);
        // this.setState({
        //     arr: arr
        // });
    }
    animateBoxes(animArr) {
        let step = 0;
        let arr = this.state.arr;
        let animTimer = setInterval(() => {
            animArr.map(item => {
                arr[item.row][item.column].style = {
                    ...arr[item.row][item.column].style,
                    top: arr[item.row][item.column].style.top + (item.toValue / 10)
                };
            })
            this.setState({
                arr: arr
            })
            step === 10 ? clearInterval(animTimer) : step++;
        }, 20);
    }
    createLevel(){
        var arr = [];
        for(var i =0; i < gridCount; i++){
            let rowArr = [];
            for(var j=0; j < gridCount; j++){
                var type= 'pop';
                if((i + j % 5) === 0) type = 'bomb';
                else if((i + j % 4) === 0) type = 'vertical';
                else if((i + j % 3) === 0) type = 'horizontal';
                rowArr.push({
                    type: type,
                    img: (i+j) % 3 === 0 ? 'pop2' : (i + j) % 4 === 0 ? 'pop3' : 'pop',
                    className: type === 'pop' ? 'brick' : '',
                    isPopped: false,
                    style: {
                        top: (i * (gridEdge / gridCount) + brickSpacing),
                        left: brickSpacing + ((width-gridEdge) / 2) + j * (gridEdge / gridCount),
                        height: gridEdge / gridCount - brickSpacing,
                        width: gridEdge / gridCount - brickSpacing,
                        borderRadius: brickSpacing,
                        position: 'absolute'
                    }
                })
            }
            arr.push(rowArr)
        }
        this.setState({
            arr: arr
        })
    }
    getRandomColor() {
        var letters = '0123456789ABCDEF';
        var color = '#';
        for (var i = 0; i < 6; i++) {
          color += letters[Math.floor(Math.random() * 16)];
        }
        return color;
      }
    inside(point, vs) {
        // ray-casting algorithm based on
        // http://www.ecse.rpi.edu/Homepages/wrf/Research/Short_Notes/pnpoly.html
    
        var x = point[0], y = point[1];
    
        var inside = false;
        for (var i = 0, j = vs.length - 1; i < vs.length; j = i++) {
            var xi = vs[i][0], yi = vs[i][1];
            var xj = vs[j][0], yj = vs[j][1];
    
            var intersect = ((yi > y) != (yj > y))
                && (x < (xj - xi) * (y - yi) / (yj - yi) + xi);
            if (intersect) inside = !inside;
        }
    
        return inside;
    }
    relativeCoordinatesForEvent(mouseEvent) {
        const boundingRect = this.refs.drawArea.getBoundingClientRect();
        return new Immutable.Map({
            x: mouseEvent.touches[0].clientX - boundingRect.left,
            y: mouseEvent.touches[0].clientY - boundingRect.top,
        });
        }
    handleMouseDown = (mouseEvent) => {
        var touchPoint = [mouseEvent.touches[0].clientX, mouseEvent.touches[0].clientY];
        if (!this.inside(touchPoint,this.state.start)) return;
        clearInterval(this.interval)
        const point = this.relativeCoordinatesForEvent(mouseEvent);
        this.setState({
            lines: new Immutable.List()
            }, () => {
            this.setState(prevState => ({
                lines: prevState.lines.push(new Immutable.List([point])),
                isDrawing: true,
                isAnimating: false,
                relArray: new Immutable.List(),
                lineColor: "#23A6D5"
            }));
            })
        }
        handleMouseMove = (mouseEvent) => {
        if (!this.state.isDrawing) {
            return;
        }
        if(lineLength > lineMax) this.handleMouseUp()
        var touchPoint = [mouseEvent.touches[0].clientX, mouseEvent.touches[0].clientY];
        const point = this.relativeCoordinatesForEvent(mouseEvent);
        if(this.state.lines) {
            const listNumber = this.state.lines.size - 1;
            const pointNumber = this.state.lines.get(this.state.lines.size - 1) && this.state.lines.get(this.state.lines.size - 1).size - 1 || 0;
            const prevPoint = this.state.lines.getIn([listNumber, pointNumber]);
            if(prevPoint){
        const slope = (prevPoint.get('y') - point.get('y')) / (prevPoint.get('x') - point.get('x'));
        const dist = Math.sqrt(Math.pow((prevPoint.get('y') - point.get('y')), 2) + Math.pow((prevPoint.get('x') - point.get('x')), 2));
        const xDir = point.get('y') < prevPoint.get('y');
        const yDir = point.get('x') < prevPoint.get('x');
        let relSeg = [];
        if(dist > 3) {
            let flag = 0;
            let factor = 3;
            while(flag < dist) {
            flag = flag + factor;
            relSeg.push({ slope : slope, dist : factor, xDir: xDir, yDir: yDir })
            }
        } else {
            relSeg.push({ slope : slope, dist : dist, xDir: xDir, yDir: yDir })
        }
        let newSeg = new Immutable.List(relSeg)
        lineLength= lineLength + dist;
        this.setState(prevState =>  ({
            lines: prevState.lines.updateIn([prevState.lines.size - 1], line => line.push(point)),
            relArray: prevState.relArray.concat(newSeg)
        }));
        }
        }
        else this.setState(prevState =>  ({
        lines: prevState.lines.updateIn([prevState.lines.size - 1], line => line.push(point))
        }));
        }
        handleMouseUp() {
            if(!this.state.isDrawing) return;
        lineLength = 0;
        var size = this.state.lines.get(this.state.lines.size - 1) && this.state.lines.get(this.state.lines.size - 1).size;
        if(size > 8)
            this.setState({ isDrawing: false, isAlive: true } , () => this.startTimer());
        else
        this.setState({
            lines: new Immutable.List(),
            relArray : new Immutable.List(),
            isDrawing: false,
            lineColor: "#23A6D5",
            isAlive: false
            })
        }
        startTimer() {
            var relPointer = 0
        var refPoint = this.state.lines.get(this.state.lines.size - 1).get(this.state.lines.get(this.state.lines.size - 1).size - 1),
        relObj = this.state.relArray.get(relPointer)
            this.interval = setInterval(() => {
                if(!this.state.isAlive) {
                    clearInterval(this.interval)
                    this.setState({
                    lines: new Immutable.List(),
                    relArray : new Immutable.List(),
                    isDrawing: false,
                    lineColor: "#23A6D5",
                    isAlive: false
                    })
                }
                var isAlive = true;
            relObj = this.state.relArray.get(relPointer)
            relPointer ++;
            if(relPointer > this.state.relArray.size) relPointer = 0
            if(!relObj) return;
            if(isNaN(relObj.slope)) return 0;
            let newX = refPoint.get('x');
                let newY = refPoint.get('y');
                if(relObj.slope === Infinity) newY -= relObj.dist;
                else if(relObj.slope === -Infinity) newY += relObj.dist;
                else if((relObj.xDir && !relObj.yDir) || (!relObj.xDir && !relObj.yDir)){
                        newY += (relObj.dist * Math.sin(Math.atan(relObj.slope)));
                        newX += (relObj.dist * Math.cos(Math.atan(relObj.slope)));
                }
                else if((!relObj.xDir && relObj.yDir) || (relObj.xDir && relObj.yDir)) {
                    newY -= (relObj.dist * Math.sin(Math.atan(relObj.slope)));
                        newX -= (relObj.dist * Math.cos(Math.atan(relObj.slope)));
                }
                isAlive = newX < width && newX > 0 && newY > 0 && newY < height;
                isAlive = this.detectCollision(refPoint, isAlive);
                !isAlive && this.insertNew()
                refPoint = new Immutable.Map({ x: newX, y: newY })
                this.setState(prevState =>  ({
                    lines: prevState.lines.updateIn([prevState.lines.size - 1], line => line && line.push(refPoint)),
                    isAlive: isAlive
                }));
                this.setState(prevState =>  ({
                    lines: prevState.lines.updateIn([prevState.lines.size - 1], line => {
                        if(line) {
                        if(line.size > 25) {
                            return line.splice(0,2) || line.splice(0,line.size) && line
                        }
                            else return line.shift()
                        }
                    })
                }));
            },15)
        }
        detectCollision(pointer, isAlive){
            var arr = this.state.arr;
            var change = false;
            var points = 0;
            if(pointer.get('y') < gridEdge &&
            pointer.get('x') > ((width / 2) - (gridEdge / 2)) &&
            pointer.get('x') < ((width / 2) + (gridEdge / 2))) {
                var row = Math.floor(pointer.get('y') / (gridEdge / gridCount));
                var column = Math.floor((pointer.get('x') - ((width / 2) - (gridEdge / 2))) / (gridEdge / gridCount));
                
                if(!arr[row][column].isPopped){
                    switch(arr[row][column].type){
                case 'pop' : if(!arr[row][column].isPopped) {
                    change = true;
                    this.setState({
                        points: this.state.points+1
                    })
                    arr[row][column].isPopped = true;
                    arr[row][column].className = "pop";
                };
                break;
                case 'horizontal':
                this.setState({
                    points: this.state.points+gridCount
                })
                change = true;
                for(var i=0; i< gridCount; i++){
                    arr[row][i].isPopped = true;
                    arr[row][i].className = 'pop';
                }
                break;
                case 'vertical':
                this.setState({
                    points: this.state.points+1
                })
                change = true;
                for(var i=0; i< gridCount; i++){
                    arr[i][column].isPopped = true;
                    arr[i][column].className = 'pop';
                }
                break;
                case 'bomb': 
                this.setState({
                    lines: new Immutable.List(),
                    relArray : new Immutable.List(),
                    isDrawing: false,
                    lineColor: "#23A6D5",
                    isAlive: false,
                    highScore: this.state.points > this.state.highScore ? this.state.points : this.state.highScore,
                    points: 0,
                    alerted: true
                    });
                break;
                }
            }
            }
            change &&
            this.setState({
                arr: arr
            })
            return isAlive;
        }
        render() {
            return (
                <div>
                    <meta name="viewport" content="initial-scale=1.0, maximum-scale=1.0, user-scalable=no" />
                <div
                    id="jaz"
                    className="drawArea"
                    ref="drawArea"
                >
                    {
                        this.state.arr.map((item,index) => {
                                return item.map((item2, index2) => {
                                    if(item2.type === 'pop')
                                        return <div key= {index2 + index} style= {item2.style} className = {item2.className} />
                                    else if(item2.type === 'bomb') return <img src={require('./bomb.png')} key= {index2 + index} style= {item2.style} className = {item2.className} />
                                    else if(item2.type === 'vertical') return <img src={require('./vertical.png')} key= {index2 + index} style= {item2.style} className = {item2.className} />
                                    else return <img src={require('./horizontal.png')} key= {index2 + index} style= {item2.style} className = {item2.className} />
                                })
                            })
                    }
                    <svg className="drawing">
                    {this.state.lines.map((line, index) => (
                        <DrawingLine key={index} line={line} stroke= {this.state.lineColor} strokeWidth={8} className="path" />
                        ))}
                    </svg>
                    <div className="startDiv" style={{ height: startHeight, width: startWidth, top: startTop, borderRadius: 20 }} />
                    <span 
                    style = {{
                        position: 'absolute',
                        top: 20,
                        left: width - 60,
                        height: 40,
                        width: 40
                    }}
                    >{'HS:' + this.state.highScore}</span>
                    <span 
                    style = {{
                        position: 'absolute',
                        top: 40,
                        left: width - 60,
                        height: 40,
                        width: 40
                    }}
                    >{'Pts:' + this.state.points}</span>
                    <span 
                    style = {{
                        position: 'absolute',
                        top: this.state.alerted ? 0 : -  height,
                        left: 0,
                        height: height,
                        width: width,
                        fontSize: 15,
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        background: 'rgba(0,0,0,0.5)',
                        color: 'white'
                    }}
                    >
                    <p>{'GAME OVER'}</p>
                    <br />
                    <span
                    onClick={() => {
                        this.setState({ alerted: false });
                    this.createLevel();
                }}
                    >{' @'}</span>
                    </span>
                </div>
                </div>
            );
        }
}
function DrawingLine({ line, stroke, strokeWidth, className }) {
    if(line.size <= 0) return null;
    const pathData = "M " +
      line
        .map(p => {
          return `${p.get('x')} ${p.get('y')}`;
        })
        .join(" L ");
    return <path className={className} d={pathData} stroke= {stroke} strokeWidth={strokeWidth} />;
}


